import { Injectable } from '@angular/core';
import { CanActivate, Router } from "@angular/router";
import { environment } from 'src/environments/environment';
import { CurrentUserService } from "./current.user.service";
/**
 * هنا ما يهمنا فقط ان يكون المستخدم مسجل الدخول للموقع
 */
@Injectable()
export class UserAuthCanActive implements CanActivate {
    constructor(private u: CurrentUserService,private router:Router) {
    }

    canActivate() {
        if (!this.u.isUserlogged) {
            this.router.navigate(['/signIn'])
        }
        return this.u.isUserlogged;
    }

}//End Class