import { BehaviorSubjectService } from '../behavior.subject.service';
import { LocalStorageService } from '../local.storage.service';
import { EventEmitter, Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IUser } from 'src/interfaces/i.user.interface';
import { Router } from '@angular/router';
import { ResourcesArModel } from 'src/models/resources/resources.ar.model';
@Injectable()
export class CurrentUserService {

    /**User Is Logged */
    isUserlogged: boolean;
    /**
     * Current User Language Code
     */
    languageCode: string;
    /**
       * Resources
       */
    r = new ResourcesArModel();
    /**
     * CUrrent User Information
     */
    get u(): IUser {
        return BehaviorSubjectService.userInformation.value;
    };


    /**
* When User Changed Language From Fotter Of Changed By Socket.io
*/
    onChangeLanguage: EventEmitter<string> = new EventEmitter<string>();

    /**
     *
     */
    constructor(private router: Router) {
        // listener User If Changed
        BehaviorSubjectService.userInformation.subscribe(res => {
            this.changedUserInformation(res);
        });
    }

    private changedUserInformation(userInfo: IUser) {
        this.isUserlogged = userInfo._id ? true : false;
    }


    /**
     * Log Out User
     */
    logOut(donotTellServer: boolean = null): void {
        LocalStorageService.setUsereInformation({} as IUser);
        LocalStorageService.removeAccessToken();
        this.router.navigate(['/']);
    }




}// End CLass
