

import { Pipe, PipeTransform, OnInit } from '@angular/core';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';

@Pipe({ name: 'sortPipe' })
/**
 * Sort By Dynamic Value From Resourcs
 */
export class SortPipe implements PipeTransform {

constructor(private u: CurrentUserService){}

    transform(items: any[], fieldName: string, reverse: boolean): any {
        if (!items || !fieldName) {
            return items;
        }

        if (reverse) {
            return items.sort((a, b) => {
                const curretnValue =  this.u.r[b[fieldName]]?.toString().toLowerCase();
                const nextValue =  this.u.r[a[fieldName]]?.toString().toLowerCase();
                if (curretnValue < nextValue) { return -1; }
                else if (curretnValue > nextValue) {  return 1; }
                return 0;
            });

        } else {

            return items.sort((a, b) => {
                const curretnValue =  this.u.r[b[fieldName]]?.toString().toLowerCase();
                const nextValue =  this.u.r[a[fieldName]]?.toString().toLowerCase();
                if (curretnValue < nextValue) { return 1; }
                else if (curretnValue > nextValue) { return -1; }
                return 0;
            });
        }
    }

}
