import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/services/local.storage.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { FormatWidth, registerLocaleData } from '@angular/common';
import localeAR_SA from '@angular/common/locales/ar-SA';
import localeEn from '@angular/common/locales/en';

import { BehaviorSubjectService } from 'src/services/behavior.subject.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styles:[
    `
    
    `
  ]
})

export class AppComponent implements OnInit {
  onLoggedUserChangeHisInformation: any;
  onLoggedUserLoggedOut:any;
  constructor(public u: CurrentUserService, private http: HttpService) { }
  ngOnInit() {
    // Load Current User Information
    this.loadCurrentUserInformation();

    this.registerLocales();
  }
  /**
   * Register Locale Here For Use In Any Component
   */
  registerLocales() {
    registerLocaleData(localeAR_SA,"ar");

  }

  /**
   * Load User Information From Local Storage And If Not Logged In I Will Set Default Lanugage For Him
   */
  loadCurrentUserInformation(): void {
    /**
     * If Not Found Language I Will Add Langage Id In Local Storage In Key "UI (User Information)"
     */
    const currentUserInformation = LocalStorageService.getUserInformation();
    if (currentUserInformation) {
      BehaviorSubjectService.userInformation.next(currentUserInformation);
    }
  }
 
}// End CLass
