import { BehaviorSubjectService } from '../../../services/behavior.subject.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { iterator } from 'rxjs/internal-compatibility';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-export-buttons',
    template: `
          <button class="btn btn-outline-primary m-1" [innerText]="u.r.print" (click)="print()"></button>
  `,
    styles: [
        `
      
        `
    ]
})
export class ExportButtonsComponent implements OnInit {

    @Input('divId') targetDivId?: string;
    styleLinks: string="";
    constructor(public u: CurrentUserService) { }
    ngOnInit(): void {
        let allStylesLink = document.querySelectorAll('title,link,style');
        for (let index = 0; index <= allStylesLink.length - 1; index++)
            this.styleLinks += allStylesLink.item(index).outerHTML;
    }

    /**
     * Print Content
     */
    print() {
        debugger
        let printWindow = window.open(null, "")
        printWindow.document.write(`
        <html >
            <head>
            <base href="${environment.website_url}">
                <style>
                @media print{
                    app-export-buttons{
                        display:none
                    }
                    }
                }
                </style>
                ${this.styleLinks}
            </head>
            <body>
            ${document.getElementById(this.targetDivId).outerHTML}
            </body>
        </html>`);
        printWindow.focus();
        setTimeout(() => {
            printWindow.print();
            printWindow.document.close();
            printWindow.close();
        }, 100);
    }


}//End Class
