import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { HomeComponent } from '../app/home/home.component';
import { PageNotFoundComponent } from 'src/app/page-not-found/page-not-found.component';
import { UserAuthCanActive } from "../services/authentcation/user.auth.can.active.service";
import { AccountSettingComponent } from "src/app/account-setting/account-setting.component";
import { SighInComponent } from 'src/app/sigh-in/sigh.in.component';
import { AdminAuthCanActive } from 'src/services/authentcation/admin.auth.can.active.service';

const ROUTS: Routes = [
    {
        path: '', component: HomeComponent,

    },
    {
        path: 'accountsetting', component: AccountSettingComponent, canActivate: [UserAuthCanActive],
        data: {
            breadcrumbs: ["accountSetting"]
        }
    },
    { path: 'signIn', component: SighInComponent },
    {
        path: 'setting',
        loadChildren: () => import('./settings.module').then(mod => mod.SettingsModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
    },
    {
        path: 'users',
        loadChildren: () => import('./users.module').then(mod => mod.UsersModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
    },
    {
        path: 'freights',
        loadChildren: () => import('./freights.module').then(mod => mod.FreightsModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
    },
    {
        path: 'financialTransfers',
        loadChildren: () => import('./financial.transfares.module').then(mod => mod.FinancialTransfersModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
    },
    {
        path: 'reports',
        loadChildren: () => import('./reports.module').then(mod => mod.ReportsModule),
        canActivate: [UserAuthCanActive],
    },
    {
        path: 'sectorSales',
        loadChildren: () => import('./sector.sales.module').then(mod => mod.SectorSalesModule),
        canActivate: [UserAuthCanActive],
    },

    { path: '**', component: PageNotFoundComponent }
];


@NgModule({
    imports: [RouterModule.forRoot(ROUTS, { relativeLinkResolution: 'legacy' })],
    exports: [RouterModule]
})
export class AppRoutingModule {

}

