import { ResourcesArModel } from 'src/models/resources/resources.ar.model';
import { IResponse } from 'src/interfaces/i.response.interface';
import { ResponseTypeEnum } from 'src/enums/response.type.enum';
import { IResources } from 'src/interfaces/i.resources.interface';

export class AlertService {


    static defultOptions: any = {
        actionText: '✖'
        //   pos: I Will change This Value Of Key From CurrentUserService

    };

    /**
     * Show Alert By Response Type
     * @param res
     */
    static showResponseMessage(res: IResponse<any>): void {
        switch (res.responseType) {
            case ResponseTypeEnum.error:
                {
                    
                    console.info(res)
                    return AlertService.errorMessage(res.message);
                }
            default:
                {
                    console.log(res)
                    return AlertService.infoMesage(res.message);
                }
        }
    }

    /**
     * Show Error Alert
     * @param message
     */
    static errorMessage(message: string): void {
        this.defultOptions.text = message;
        this.defultOptions.backgroundColor = 'rgb(231, 81, 90)';
        window['Snackbar'].show(this.defultOptions);
    }

    /**
     * Show Info Alert
     * @param message
     */
    static infoMesage(message: string): void {
        this.defultOptions.backgroundColor = null;
        this.defultOptions.text = message;
        window['Snackbar'].show(this.defultOptions);
    }

    /**
     * Show Message For : Observable Error When Request To Server
     * @param error
     */
    static canRequestError(error: any, r: IResources = new ResourcesArModel()): void {
        console.log('Error', error);
        AlertService.errorMessage(error?.message || r.iCanNotAccessToServer);
    }

}// End Class