import {
  Directive,
  HostListener,
  Output,
  EventEmitter,
  ElementRef
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { FileDragHandle } from 'src/interfaces/file.drag.handel.interface';



@Directive({
  selector: '[deopify]'
})
export class DeopifyDirective {
  /** On Selected Files Event */
  @Output() onSelectFiles: EventEmitter<FileDragHandle[]> = new EventEmitter();


  /** File Input For Fire If Click On DeopZone */
  private fileInput: HTMLInputElement;
  constructor(private sanitizer: DomSanitizer, private elm: ElementRef) {
    this.fileInput = document.createElement('input');
    this.fileInput.type = 'file';
    this.fileInput.accept = "image/*";
  }

 


  @HostListener('dragover', ['$event'])
  public onDragOver(evt: DragEvent) {
    evt.preventDefault();
    evt.stopPropagation();
  }

  @HostListener('drop', ['$event'])
  public onDrop(evt: DragEvent) {
    evt.preventDefault();
    evt.stopPropagation();

    // Pass Files Now To Compoennt
    this.passFilesToComponent(evt.dataTransfer.files);
  }


  @HostListener('click', ['$event'])
  public onClick() {
    this.fileInput.click();
    this.fileInput.onchange = (ev) => {
      this.passFilesToComponent(this.fileInput.files);
    };
  }

  /** Read File And Pass To Compoenent */
  passFilesToComponent(files: FileList) {
    const newFile: FileDragHandle[] = [];
    for (let i = 0; i < files.length; i++) {
      newFile.push(
        {
          file: files[i],
          url: this.sanitizer.bypassSecurityTrustUrl(window.URL.createObjectURL(files[i])),
        } as FileDragHandle);
    }
    debugger;
   this.onSelectFiles.emit(newFile);
  }

}// End Class
