import { LocalStorageKeys } from '../consts/local.storage.keys.const';
import * as decode from 'jwt-decode';
import { BehaviorSubjectService } from './behavior.subject.service';
import { IUser } from 'src/interfaces/i.user.interface';
export class LocalStorageService {


  /**
   * Get Access Token
   */

  static get accessToken(): string { return localStorage.getItem(LocalStorageKeys.userAccessToken); }

  /**
   * Save Access Token In Loacl Storage
   */
  static set accessToken(accessToken: string) {
    // Save Access Token
    localStorage.setItem(LocalStorageKeys.userAccessToken, accessToken);

    // Decode User Information For Upate Header And Resources And Another
    this.setUsereInformation(decode(accessToken));

  }

  /**
   * Remove Access Token
   */
  static removeAccessToken(): void {
    localStorage.removeItem(LocalStorageKeys.userAccessToken);
  }

  /**
   * Save User Login Informarion In Local Storage
   * @param userInfo
   */
  static setUsereInformation(userInfo: IUser): void {
    let userData = {
      _id: userInfo._id,
      userName: userInfo.userName,
      fullName: userInfo.fullName,
      picturePath: userInfo.picturePath,
      languageCode: userInfo.languageCode,
      userTypeId:userInfo.userTypeId
    } as IUser;
    localStorage.setItem(LocalStorageKeys.currentUserInformation, JSON.stringify(userData));
    // Update User Info
    BehaviorSubjectService.userInformation.next(userData);
  }

  /**
   * Get Current User Information Saved
   */
  static getUserInformation(): IUser {
    const userInfo = localStorage.getItem(LocalStorageKeys.currentUserInformation);
    if (userInfo) {
      return JSON.parse(userInfo);
    }
    return null;
  }

  /**
   * Set User Lanuge And The Default Lanugage Is English
   */
  static setLanugage(newLanguageCode: string): void {
    let userInfo = this.getUserInformation();
    if (userInfo) {
      // Update Current User Information
      userInfo.languageCode = newLanguageCode;
    } else {
      // Set New User Information
      userInfo = ({
        languageCode: newLanguageCode
      } as IUser);
    }
    this.setUsereInformation(userInfo);
  }


  /**
   * SET: Number Of Result In Page
   */
  static set fopNumberOfDisplayResultInPage(val: number) {
    localStorage.setItem(LocalStorageKeys.fopNumberOfDisplayResultInPage, val.toString());
  }

  /**
   * GET: Number Of Result In Page
   */
  static get fopNumberOfDisplayResultInPage(): number {
    let data = JSON.parse(localStorage.getItem(LocalStorageKeys.fopNumberOfDisplayResultInPage));
    if (data)
      return Number(data);
    return data;
  }


}// end class
