import { Injectable } from '@angular/core';
import Swal, { SweetAlertOptions, SweetAlertResult } from 'sweetalert2'
import { CurrentUserService } from './authentcation/current.user.service';

@Injectable()
export class SweetAlertService {
    constructor(public u: CurrentUserService) { }
    public confirm(text: string, confirmButtonText?: string, cancelButtonText?: string): Promise<boolean> {
        return new Promise((resolve: any, reject) => {
            Swal.fire({
                // title: title,
                text: text,
                type: 'warning',
                showCancelButton: true,
                confirmButtonText: confirmButtonText || this.u.r.ok,
                cancelButtonText: cancelButtonText || this.u.r.cancel,
                padding: '2em'
            } as SweetAlertOptions).then((result: SweetAlertResult) => {
                //Resolve When Ok Only
                if (result.value) {
                    resolve();
                }
            })

        })
    }


}//End Class