import { SharedComponents } from 'src/app/shared.components';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { publish } from 'rxjs/operators';
import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';
import { SEOService } from 'src/services/seo.service';
import { ISEO } from 'src/interfaces/i.seo.interface';
import { HttpService } from 'src/services/http/http.service';
import { IUser } from 'src/interfaces/i.user.interface';
import { AlertService } from 'src/services/alert.service';
import { LocalStorageService } from 'src/services/local.storage.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sigh-in',
  templateUrl: './sigh.in.component.html',
  styleUrls: [
  ]
})
export class SighInComponent extends SharedComponents implements OnInit {

  env = environment;
  signInForm: FormGroup = new FormGroup({
    userName: new FormControl(null, [Validators.required]),
    password: new FormControl(null, Validators.required),
    showPassword: new FormControl(false)
  });
  formSubmitError: boolean = false;
  constructor(public u: CurrentUserService, private seo: SEOService, private http: HttpService,private router:Router) { super() }

  ngOnInit(): void {
    this.seo.updateSEO("signIn");
  }


  /**
   * Sign In
   */
  signIn(): void {
    if (this.signInForm.invalid) {
      this.formSubmitError = true;
      return;
    }

    this.signInForm.disable();
    this.allSubscriptions.push(this.http.signIn<string>(this.signInForm.value as IUser).subscribe(res => {
      debugger;
      AlertService.showResponseMessage(res);
      if (res.isSuccess) {
        LocalStorageService.accessToken = res.result;
        this.router.navigate(['/']);
      }
    }, error => {
      AlertService.canRequestError(error);
    }, () => {
      this.signInForm.enable();
    }))
  }

}//End CLass
