import { BehaviorSubjectService } from './../../../services/behavior.subject.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';

@Component({
    selector: 'app-breadcrumb',
    template: `
         <nav class="breadcrumb-two" aria-label="breadcrumb">
            <ol class="breadcrumb">
                <li class="breadcrumb-item"><a routerLink="/"    [innerHtml]="u.r.home"></a></li>
                <li class="breadcrumb-item " *ngFor="let item of items;last as isLast" [ngClass]="{'active': isLast&&!allowStaticBreadcrumd}">
                    <a   [innerHtml]="u.r[item]" ></a></li>
                    <li class="breadcrumb-item active"  *ngIf="allowStaticBreadcrumd"><a   [innerHtml]="title.getTitle()" ></a></li>
            </ol>
        </nav>
  `,
    styles: [
    ]
})
export class BreadcrumbComponent implements OnInit {
    items = [];
    thisPageIsNotInMenu: boolean;
    allowStaticBreadcrumd:boolean;

    constructor( public u: CurrentUserService,public title:Title) { }
    ngOnInit(): void {
        
        BehaviorSubjectService.activePage.subscribe(res => {
            this.items = [];
            this.allowStaticBreadcrumd=res.isActiveByNesterUrls&&res.isAllowShowBreadcrumbToNestedUrls;
            if (res.menu)
                this.items.push(res.menu.resourceKey_title);
            if (res.subMenu)
                this.items.push(res.subMenu.resourceKey_title);
        });
    }


}//End Class
