import { Injectable } from '@angular/core';
import { Meta, Title } from "@angular/platform-browser";
import { ISEO } from 'src/interfaces/i.seo.interface';
import { CurrentUserService } from './authentcation/current.user.service';
@Injectable()
/**
 * SEO Service
 */
export class SEOService {

    constructor(private meta: Meta, private title: Title,private u:CurrentUserService) { }

    /** Current Seo */
    currentSeo: ISEO;
    /**
     * Update Static Seo Like ('Home Page','PostsPage','VideosPage',....)
     * @param seo 
     */
    updateSEO(pageTitleKey:string): void {
        //Save Current Seo Information For Refresh Affter Cchange Resources
        this.title.setTitle(this.u.r[pageTitleKey]);
    }

     
 
}//End Class