import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { FileDragHandle } from 'src/interfaces/file.drag.handel.interface';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { LocalStorageService } from 'src/services/local.storage.service';
import { SEOService } from 'src/services/seo.service';
import { ISEO } from 'src/interfaces/i.seo.interface';
import { IUser } from 'src/interfaces/i.user.interface';
import { SharedComponents } from "src/app/shared.components";
import { environment } from 'src/environments/environment';
import { CustomValidatorsService } from 'src/services/custom.validators.service';
@Component({
  selector: 'app-account-setting',
  templateUrl: './account-setting.component.html',
  styleUrls: [
    // "../../assets/plugins/dropify/dropify.min.css",
    //"../../assets/css/account-setting.ltr.css",

  ]
})
export class AccountSettingComponent extends SharedComponents implements OnInit {
  userInfo: IUser = {} as IUser;
  form: FormGroup;
  formSubmitError: boolean;
  newPicture: FileDragHandle = { } as FileDragHandle ;
  constructor(public u: CurrentUserService, private http: HttpService, private seo: SEOService) { super(); }

  ngOnInit(): void {

    this.seo.updateSEO("accountSetting");

    //Get Account Information
    this.getAccountInformationForUpdate();

    //Init Form Group
    this.form = new FormGroup({
      userName: new FormControl(null),
      fullName: new FormControl(null, Validators.maxLength(100)),
      email: new FormControl(null, [Validators.required, Validators.email]),
      phoneNumber: new FormControl(null,CustomValidatorsService.phoneNumber),
      password: new FormControl(null, [Validators.maxLength(50), Validators.minLength(5)]),
    });

  }

  /**
   * Get Current Account Information For Update
   */
  getAccountInformationForUpdate(): void {
    this.allSubscriptions.push(
      this.http.getCurrentUserInformationForUpdate<IUser>().subscribe(res => {
        if (!res.isSuccess) {
          AlertService.showResponseMessage(res);
          return;
        }

        this.userInfo = res.result;
        this.passValeToFormGroup();

      }, error => {

        AlertService.canRequestError(error);
      }));
  }

  /** Pass Value To From Group */
  passValeToFormGroup() {
    this.form.get('userName').setValue(this.userInfo.userName);
    this.form.get('userName').disable();
    this.form.get('fullName').setValue(this.userInfo.fullName);
    this.form.get('email').setValue(this.userInfo.email);
    this.form.get('phoneNumber').setValue(this.userInfo.phoneNumber);
    this.newPicture.file = null;
    this.newPicture.url = this.userInfo.picturePath;
  }

  /**
   * Save Change
   */
  saveChange(): void {

    //Check Form Is Valid
    if (this.form.invalid) {
      AlertService.errorMessage(this.u.r.youHaveSomeErrors);
      this.formSubmitError = true;
      return;
    }

    this.userInfo.actionDisapled = true;
    this.form.disable();
    let fomrD = new FormData();
    if (this.form.controls.fullName.value)
      fomrD.append('fullName', this.form.controls.fullName.value);
    fomrD.append('email', this.form.controls.email.value);
    fomrD.append('userName', this.form.controls.userName.value);
    fomrD.append('phoneNumber', this.form.controls.phoneNumber.value);
    fomrD.append('password', this.form.controls.password.value);
    if (this.newPicture.file)
      fomrD.append('image', this.newPicture.file);


    this.allSubscriptions.push(
      this.http.updateUserInformtion<IUser>(fomrD).subscribe(res => {
        this.userInfo.actionDisapled = false;
        this.form.enable();
        this.form.controls.userName.disable();
        if (res.isSuccess) {
          this.form.reset();
          this.userInfo = res.result;
          this.passValeToFormGroup();
          LocalStorageService.setUsereInformation(this.userInfo);
        }
        AlertService.showResponseMessage(res);

      }))
  }

  /** Show And Hide Password Toggel */
  showPasswordToggal(elementTrget: HTMLInputElement) {
    if (elementTrget.type == "password")
      elementTrget.type = "text";
    else
      elementTrget.type = "password";
  }


}//End Class
