import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { environment } from 'src/environments/environment';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { SectorSaleService } from 'src/services/sector.sale.service';
import * as tableExport from "table-export";

@Component({
  selector: 'fop-header',
  templateUrl: './fop-header.component.html',
  styles: []
})
export class FopHeaderComponent implements OnInit {
  /**
   * 
   */
  @Output() searchText = new EventEmitter();
  searchValue: string;
  @Input() tableId?: string;
  styleLinks: string="";


  constructor(public u: CurrentUserService) { }

  ngOnInit(): void {
    let allStylesLink = document.querySelectorAll('title,link,style');
        for (let index = 0; index <= allStylesLink.length - 1; index++)
            this.styleLinks += allStylesLink.item(index).outerHTML;
  }

  //Output Search Text
  sendSearchText(val: string): void {
    this.searchText.emit(val);
  }

  /**Export Table To Excel */
  exportAs(exportType: string) {
    tableExport(this.tableId, document.title + " " + new Date().toDateString().replace(/\//g, '-'), exportType);
  }


  /**
   * Print
   */
  print() {
    let printWindow = window.open(null, "")
    printWindow.document.write(`
    <html >
        <head>
        <base href="${environment.website_url}">
            <style>
            @media print{
                app-export-buttons{
                    display:none
                }
                }
            }
            </style>
            ${this.styleLinks}
        </head>
        <body>
        ${document.getElementById(this.tableId).outerHTML}
        </body>
    </html>`);
    printWindow.focus();
    setTimeout(() => {
      printWindow.print();
      printWindow.document.close();
      printWindow.close();
    }, 100);
  }

}//End Class
