import { BehaviorSubjectService } from 'src/services/behavior.subject.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { MenuService } from "src/services/menu.service";
import { Location } from '@angular/common';
import { IMenu } from 'src/interfaces/i.menu.interface';
import { filter } from 'rxjs/operators';
@Component({
  selector: 'app-sid-bar',
  templateUrl: './sid-bar.component.html',
  styles: [
  ]
})
export class SidBarComponent implements OnInit {

  menus: IMenu[] = [];

  constructor(public u: CurrentUserService, private router: Router, private location: Location) { }

  ngOnInit(): void {
    this.menus = MenuService.getMenu().filter(m => !m.userTypesId || m.userTypesId.findIndex(t => t == this.u.u.userTypeId) >= 0);
    this.router.events
      .pipe(filter(reo => reo instanceof NavigationEnd))
      .subscribe(r => {
        BehaviorSubjectService.activePage.next({} as any)

        //Check If This Home Page
        let menuIsFound = false;
        this.menus.forEach(m => {
          m.isActive = false;
          if (menuIsFound) return;
          if (m.isLonly) {
            //Active Menu
            let isActiveByBasicUrl = m.url.toLowerCase() == r['url'].toLowerCase();
            let isActiveByNesterUrls = m.nestedurls?.findIndex(url => url.test(r['url'])) > -1;
            m.isActive = isActiveByBasicUrl || isActiveByNesterUrls;
            if (m.isActive)
              menuIsFound = true;
            BehaviorSubjectService.activePage.next({ menu: m, subMenu: null, isActiveByNesterUrls: isActiveByBasicUrl, isActiveByBasicUrl: isActiveByBasicUrl, isAllowShowBreadcrumbToNestedUrls: true })
          } else {
            m.subMenus.forEach(sub => {
              sub.isActive = false;
              if (menuIsFound) return
              //Active Submenu
              let isActiveByBasicUrl = sub.url.toLowerCase() == r['url'].toLowerCase();
              let isActiveByNesterUrls = sub.nestedurls?.findIndex(url => url.test(r['url'])) > -1;
              sub.isActive = isActiveByBasicUrl || isActiveByNesterUrls;
              //Active Parent
              m.isActive = sub.isActive;
              if (sub.isActive) {
                menuIsFound = true;
                BehaviorSubjectService.activePage.next({ menu: m, subMenu: sub, isActiveByNesterUrls: isActiveByNesterUrls, isActiveByBasicUrl: isActiveByBasicUrl, isAllowShowBreadcrumbToNestedUrls: sub.isAllowShowBreadcrumbToNestedUrls })
              }
            });
          }
        });

        //Init App For Redesign Menu
        setTimeout(() => {
          window['App'].initSideBar();
        }, 1000);
      });
  }


}//End CLass
