import { Component, OnInit, Input } from '@angular/core';
import { FOPModel } from 'src/models/fop/fop.model';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';

@Component({
  selector: 'fop-sort',
  templateUrl: './fop-sort.component.html',
  styleUrls: ['./fop-sort.component.css']
})
export class FopSortComponent implements OnInit {

  @Input() fop: FOPModel;
  @Input() filedName: string;
  @Input() fieldText: string;


  constructor(public u:CurrentUserService) { }
  ngOnInit() {
    //Add Field Name To Array Fro Using in Filter 
    this.fop.fieldNames.push(this.filedName);
  }

  //Sort
  sort() {
    this.fop.sort.fieldName = this.filedName;
    this.fop.sort.reverse = !this.fop.sort.reverse;
  }
}
