  constructor(private http: HttpClient) { }

  /**
 * User Sign In
 * @param userLoginInfo User Sign In Data
 */
  signIn<T>(userInfo: IUser): Observable<IResponse<T>> {
    return this.http.post<IResponse<T>>(`${environment.api_url}api/user/signInByUserName`, JSON.stringify(userInfo), { observe: 'response' })
      .pipe(map(res => res.body));
  }

  /**
  * Create A New User
  * @param userLoginInfo Create A New User
  */
  createUser<T>(userData: FormData): Observable<IResponse<T>> {
    return this.http.post<IResponse<T>>(`${environment.api_url}api/user/createUser`, userData, { observe: 'response' })

      .pipe(map(res => res.body));
  }

